import Dexie from 'dexie';
import { BaseSchemaTypes } from '../models/NodeSchema';
import { ItemSlotPair } from '../models/NodeSlot';
import { Scriplet } from '../models/Scriplet';

class VisualScriptingDB extends Dexie {
    public scriplets: Dexie.Table<Scriplet, number>;

    constructor() {
        super("VisualScriptingDB");

        this.version(1).stores({
            scriplets: "++id, name"
        });
        this.open().catch(error => {
            console.error(`Opening the db '${this.name}' failed: ${error}`);
        });

        this.scriplets = this.table("scriplets");
        this.scriplets.mapToClass(Scriplet);

        this.seedData();
    }

    seedData() {
        /*
        NodeConnections.addConnection(2, 1, 1, 0);
        NodeConnections.addConnection(4, 0, 2, 0);
        NodeConnections.addConnection(3, 1, 2, 0);
        NodeConnections.addConnection(4, 1, 3, 1);
        NodeConnections.addConnection(5, 0, 4, 0);
        NodeConnections.addConnection(7, 0, 4, 0);
        NodeConnections.addConnection(7, 1, 6, 0);
        NodeConnections.addConnection(8, 0, 7, 0);
        */

        this.scriplets.toArray().then(scriplets => {
            if (scriplets.length <= 0) {
                this.addScriplet("Test").then(scriplet => {
                    scriplet.addNodes(true,
                        {x: 0, y: 100, schemaId: BaseSchemaTypes.Input, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 2, slotId: 1}]]]), outputFlowSlots: []}, 
                        {x: 250, y: 100, schemaId: BaseSchemaTypes.SubtractNumbers, inputValues: [], inputSlots: [{id: 1, itemId: 1, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 4, slotId: 0}, {id: 0, itemId: 3, slotId: 1}]]]), outputFlowSlots: []},
                        {x: 500, y: 250, schemaId: BaseSchemaTypes.PrintNumber, inputValues: [], inputSlots: [{id: 1, itemId: 2, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[1, [{id: 1, itemId: 4, slotId: 1}]]]), outputFlowSlots: []},
                        {x: 750, y: 100, schemaId: BaseSchemaTypes.MultiplyNumbers, inputValues: [], inputSlots: [{id: 0, itemId: 2, slotId: 0}, {id: 1, itemId: 3, slotId: 1}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 5, slotId: 0}, {id: 0, itemId: 7, slotId: 0}]]]), outputFlowSlots: []},
                        {x: 1000, y: 100, schemaId: BaseSchemaTypes.Output, inputValues: [], inputSlots: [{id: 0, itemId: 4, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: []},
                        {x: 750, y: 350, schemaId: BaseSchemaTypes.Input, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 7, slotId: 1}]]]), outputFlowSlots: []},
                        {x: 1000, y: 300, schemaId: BaseSchemaTypes.AddNumbers, inputValues: [], inputSlots: [{id: 0, itemId: 4, slotId: 0}, {id: 1, itemId: 6, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 8, slotId: 0}]]]), outputFlowSlots: []},
                        {x: 1250, y: 300, schemaId: BaseSchemaTypes.Output, inputValues: [], inputSlots: [{id: 0, itemId: 7, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: []}
                    );
                });
            }
        });
    }

    async getScriplet(id: number): Promise<Scriplet | undefined> {
        return await this.scriplets.get(id);
    }

    async getScriplets(): Promise<Scriplet[]> {
        return await this.scriplets.toArray();
    }

    async addScriplet(name: string): Promise<Scriplet> {
        const scriplet = new Scriplet(name);
        const id = await this.scriplets.add(scriplet);
        scriplet.id = id;
        return scriplet;
    }

    async updateScriplet(id: number, changes: {[keyPath: string]: any}): Promise<boolean> {
        const result = await this.scriplets.update(id, changes);
        if (result === 0) {
            console.error(`The scriplet of id '${id}' could not be found in the database; nothing was updated.`);
            return false;
        }

        return true;
    }
    
    async updateEntireScriplet(scriplet: Scriplet): Promise<boolean> {
        const result = await this.scriplets
                                .where(":id")
                                .equals(scriplet.id)
                                .modify((value, ref) => ref.value = scriplet);
        
        if (result === 0) {
            console.error(`The scriplet of id '${scriplet.id}' could not be found in the database; nothing was updated.`);
            return false;
        }

        return true;
    }

    async deleteScriplet(id: number) {
        await this.scriplets.delete(id);
    }
}

export default new VisualScriptingDB();