import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import NodeSchemaManager from '../managers/NodeSchemaManager';
import Scrollbar from 'react-scrollbars-custom';
import { BaseNodeSchema } from '../models/NodeSchema';
import CategoryManager, { Category } from '../managers/CategoryManager';

export type SchemaCategoryProps = {
    category: Category;
    depth: number;
    addNodeItem: (id: string) => any;
}

export type SchemaCategoryState = {
    showChildren: boolean;
}

class SchemaCategory extends React.Component<SchemaCategoryProps, SchemaCategoryState> {
    constructor(props: SchemaCategoryProps) {
        super(props);

        this.state = {
            showChildren: true
        }

        this.toggleCategory = this.toggleCategory.bind(this);
        this.openCategory = this.openCategory.bind(this);
        this.closeCategory = this.closeCategory.bind(this);
    }

    toggleCategory() {
        if (this.state.showChildren)
            this.closeCategory();
        else
            this.openCategory();
    }

    openCategory() {
        this.setState({showChildren: true});
    }

    closeCategory() {
        this.setState({showChildren: false});
    }

    render() {
        const { category, depth, addNodeItem } = this.props;
        const { showChildren } = this.state;
        if (category.name.toLowerCase() === "hidden")
            return null;
        return (
            <div style={{marginLeft: `${category.name === "root" ? -1 : 0}rem`}}>
                {category.name !== "root" && (
                    <div className="flex flex-row items-center rounded-sm border-b bg-gray-900 hover:bg-gray-800 text-gray-100 cursor-pointer select-none" 
                            style={{borderColor: "#111111"}}
                            onClick={this.toggleCategory}>
                        <div className="px-2 text-xs">
                            <FontAwesomeIcon icon="chevron-down" className={`${showChildren ? "hidden" : ""}`} />
                            <FontAwesomeIcon icon="chevron-up" className={`${showChildren ? "" : "hidden"}`} />
                        </div>
                        <div className="text-sm">
                            {category.name}
                        </div>
                    </div>
                )}
                <div className={`${showChildren ? "" : "hidden"} pl-4 border-l border-dashed border-gray-700`}>
                    <div className="">
                        {category.subCategories.map((subCategory, index) => (
                            <SchemaCategory key={`category-${depth}-${index}`}
                                            category={subCategory} 
                                            depth={depth + 1}
                                            addNodeItem={addNodeItem} />
                        ))}
                    </div>
                    <div className="">
                        {category.contentChildren.map((child, index) => (
                            <div key={`child-${depth}-${index}`} 
                                className="px-2 rounded-sm hover:bg-gray-700 text-gray-100 cursor-pointer select-none"
                                onClick={event => {
                                    event.stopPropagation();
                                    addNodeItem(child.id);
                                }}>
                                {child.name}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        );
    }
}


export type SchemaSelectorProps = {
    isVisible: boolean;
    x: number;
    y: number;
    addNodeItem: (id: string) => any;
}

export type SchemaSelectorState = {
    searchValue: string;
    schemaResults: BaseNodeSchema[];
}

// TODO: Categorized selector
export default class SchemaSelector extends React.Component<SchemaSelectorProps, SchemaSelectorState> {
    constructor(props: SchemaSelectorProps) {
        super(props);

        this.state = {
            searchValue: "",
            schemaResults: NodeSchemaManager.getBaseSchemas()
        }

        this.onSearchChange = this.onSearchChange.bind(this);
        this.onSearchReset = this.onSearchReset.bind(this);
        this.filterSearch = this.filterSearch.bind(this);
    }

    componentDidMount() {
        this.filterSearch("");
    }

    onSearchChange(event: React.ChangeEvent<HTMLInputElement>) {
        const searchValue = event.target.value;
        this.filterSearch(searchValue);
    }

    onSearchReset() {
        this.filterSearch("");
    }

    filterSearch(searchValue: string) {
        let schemas = NodeSchemaManager.getBaseSchemas();
        schemas = schemas.filter(schema => (schema.name.toLowerCase().includes(searchValue.toLowerCase()) || 
                                            schema.id.toLowerCase().includes(searchValue.toLowerCase())) &&
                                            !schema.id.toLowerCase().includes("hidden"));
        this.setState({schemaResults: schemas, searchValue: searchValue});
    }

    render() {
        const { isVisible, x, y, addNodeItem } = this.props;
        const { schemaResults } = this.state;

        const width = 300;
        const height = 250;

        const rootCategory = CategoryManager.getRootSchemaCategory(schemaResults);
        return (
            <div className={`${isVisible ? "flex" : "hidden"} flex-col absolute rounded bg-gray-900 bg-opacity-90`} 
                    style={{top: y, left: x, width: `${Math.min(width, window.innerWidth - x)}px`, height: `${Math.min(height, window.innerHeight - y)}px`}}>
                <div className="p-3 h-12 flex flex-row w-full">
                    <div className="px-2 rounded-l-full bg-gray-100">
                        <FontAwesomeIcon icon="search" 
                                            className="text-sm text-gray-700" />
                    </div>
                    
                    <input type="text" 
                                value={this.state.searchValue}
                                className="w-full bg-gray-100 border-none text-sm"
                                onChange={this.onSearchChange} />
                    <button className="px-2 flex items-center rounded-r-full bg-gray-100"
                            onClick={this.onSearchReset}>
                        <FontAwesomeIcon icon="times-circle" 
                                            className="text-sm text-gray-700" />
                    </button>
                </div>
                <Scrollbar noScrollX>
                    <div className="pb-4">
                        <SchemaCategory category={rootCategory} depth={0} addNodeItem={addNodeItem} />
                    </div>
                </Scrollbar>
            </div>
        );
    }
}