import { INodeItem, NodeItem } from "./NodeItem";
import { BaseSchemaTypes } from "./NodeSchema";
import VisualScriptingDB from "../managers/VisualScriptingDB";
import { IdValuePair, ItemSlotPair } from "./NodeSlot";

export class Scriplet {
    public id: number;
    public name: string;
    public start: INodeItem;
    public items: Map<number, INodeItem>;
    public itemCounter: number;

    constructor(name: string) {
        this.id = 0;
        this.itemCounter = 1;

        this.name = name;
        this.items = new Map<number, INodeItem>();
        this.start = {id: 0, x: 0, y: 0, schemaId: BaseSchemaTypes.Start, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: []};
        this.items.set(0, this.start);
    }

    addNode(x: number, y: number, schemaId: string, inputValues: IdValuePair[], inputSlots: ItemSlotPair[], inputFlowSlots: ItemSlotPair[], outputSlots: Map<number, ItemSlotPair[]>, outputFlowSlots: ItemSlotPair[], customName?: string) {
        this.addNodes(false, {x, y, schemaId, inputValues, inputSlots, inputFlowSlots, outputSlots, outputFlowSlots, customName});
    }

    // TODO: reloadOnFinish is only needed when seeding the db, seems hackish...
    addNodes(reloadOnFinish: boolean, ...nodes: {x: number, y: number, schemaId: string, inputValues: IdValuePair[], inputSlots: ItemSlotPair[], inputFlowSlots: ItemSlotPair[], outputSlots: Map<number, ItemSlotPair[]>, outputFlowSlots: ItemSlotPair[], customName?: string}[]) {
        nodes.forEach(node => {
            const key = this.itemCounter++;
            this.items.set(key, {id: key, x: node.x, y: node.y, schemaId: node.schemaId, inputValues: node.inputValues, inputSlots: node.inputSlots, inputFlowSlots: node.inputFlowSlots, outputSlots: node.outputSlots, outputFlowSlots: node.outputFlowSlots, customName: node.customName});
        });

        VisualScriptingDB.updateScriplet(this.id, {"items": this.items, "itemCounter": this.itemCounter}).then(result => {
            if (result && reloadOnFinish)
                window.location.reload();
        });
    }

    getStart(): NodeItem {
        if (!(this.start as NodeItem).schema) {
            const newNode = new NodeItem(this.start.id, this.start.x, this.start.y, this.start.schemaId, this.start.inputValues, this.start.inputSlots, this.start.inputFlowSlots, this.start.outputSlots, this.start.outputFlowSlots, this.start.customName);
            this.start = newNode;
            return newNode;
        }
        return this.start as NodeItem;
    }

    getNode(itemId: number): NodeItem | undefined {
        const result = this.items.get(itemId);
        if (!result)
            return;
        
        return this.convertToNodeItem(result);
    }

    getNodes(): NodeItem[] {
        const result: NodeItem[] = [];
        this.items.forEach(node => {
            result.push(this.convertToNodeItem(node));
        });

        return result;
    }

    convertToNodeItem(node: INodeItem): NodeItem {
        if (!(node as NodeItem).schema) {
            const newNode = new NodeItem(node.id, node.x, node.y, node.schemaId, node.inputValues, node.inputSlots, node.inputFlowSlots, node.outputSlots, node.outputFlowSlots, node.customName);
            this.items.set(node.id, newNode);
            return newNode;
        }

        return node as NodeItem;
    }

    updateNodes() {
        VisualScriptingDB.updateScriplet(this.id, {"items": this.items});
    }

    deleteNode(itemId: number): boolean {
        return this.deleteNodes(itemId);
    }

    deleteNodes(...itemIds: number[]): boolean {
        let result = true;
        itemIds.forEach(itemId => {
            if(!this.items.delete(itemId))
                result = false;
        });

        VisualScriptingDB.updateScriplet(this.id, {"items": this.items});
        return result;
    }
}