import React from 'react';
import Node from './Node';
import { Stage, Layer, Group, Rect, Text } from 'react-konva';
import { Stage as PortalStage, Layer as PortalLayer, Portal } from 'react-konva-portal';
import Edge from './Edge';
import Colors from '../utils/Colors';
import { KonvaEventObject } from 'konva/types/Node';
import NodeConnections from '../managers/NodeConnections';
import ScripletManager from '../managers/ScripletManager';
import Konva from 'konva';
import FontFaceObserver from 'fontfaceobserver';
import { KonvaForm } from './KonvaForm';
import SchemaSelector from './SchemaSelector';
import { Vector2d } from 'konva/types/types';
import { ItemSlotPair } from '../models/NodeSlot';

export type ScripletRootProps = {
    scripletId: number;
}

export type ScripletRootState = {
    isDirtyDOM: boolean;
    isReadyToMount: boolean;
    isReady: boolean;
    contextMenu: Konva.Node | null;
    inputElement: HTMLInputElement | null;
    isContextMenuShown: boolean;
    contextMenuPos: Vector2d;
    nodeInitPos: Vector2d;
}

export default class ScripletRoot extends React.Component<ScripletRootProps, ScripletRootState> {
    private stageContainer: React.RefObject<HTMLDivElement>;
    private stage: React.RefObject<Stage>;

    constructor(props: ScripletRootProps) {
        super(props);

        this.stageContainer = React.createRef<HTMLDivElement>();
        this.stage = React.createRef<Stage>();

        this.state = {
            isDirtyDOM: false,
            isReadyToMount: false,
            isReady: false,
            contextMenu: null,
            inputElement: null,
            isContextMenuShown: false,
            contextMenuPos: {x: 0, y: 0},
            nodeInitPos: {x: 0, y: 0}
        };

        this.updateDOM = this.updateDOM.bind(this);
        this.addNodeItem = this.addNodeItem.bind(this);
        this.deleteNodeItem = this.deleteNodeItem.bind(this);
        this.handleDragNode = this.handleDragNode.bind(this);
        this.handleDeleteEdge = this.handleDeleteEdge.bind(this);
        this.handleDeleteFlowEdge = this.handleDeleteFlowEdge.bind(this);
        this.handleZoom = this.handleZoom.bind(this);
        this.handleContextMenu = this.handleContextMenu.bind(this);
        this.handleCancelContextMenu = this.handleCancelContextMenu.bind(this);
        this.fitToParent = this.fitToParent.bind(this);
    }

    componentDidMount() {
        ScripletManager.selectScriplet(this.props.scripletId).then(result => {
            console.log(result);
            if (!result)
                return;

            this.setState({isReadyToMount: true});
        });
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.fitToParent);
    }

    componentDidUpdate() {
        if (this.state.isReadyToMount) {
            window.addEventListener('resize', this.fitToParent);
            this.fitToParent();

            const stage = this.stage.current?.getStage();
            if (stage) {
                const fontAwesomeObserver = new FontFaceObserver("Font Awesome 5 Free");
                // This is necessary to show the fontawesome icons at start
                fontAwesomeObserver.load().then(() => {
                    console.log("Font Awesome 5 has loaded!");
                    stage.draw();
                }, () => {
                    console.error("Font Awesome 5 has not loaded!");
                });
            }

            this.setState({isReadyToMount: false, isReady: true});
        }

        if (this.state.isDirtyDOM) {
            this.setState({isDirtyDOM: false});
        }
    }

    updateDOM() {
        this.setState({isDirtyDOM: true});
    }

    addNodeItem(id: string) {
        const { nodeInitPos } = this.state;
        const scriplet = ScripletManager.currentScriplet;
        if (!scriplet) {
            console.error(`The current scriplet of id '${this.props.scripletId}' could not be found.`);
            return;
        }

        scriplet.addNode(nodeInitPos.x, nodeInitPos.y, id, [], [], [], new Map<number, ItemSlotPair[]>(), []);
        this.handleCancelContextMenu();
        this.updateDOM();
    }

    deleteNodeItem(id: number) {
        const scriplet = ScripletManager.currentScriplet;
        if (!scriplet) {
            console.error(`The current scriplet of id '${this.props.scripletId}' could not be found.`);
            return;
        }

        const item = scriplet.getNode(id);
        if (!item) {
            console.error(`The node item of id '${id}' could not be found.`);
            return;
        }

        const schema = item.schema;
        if (!schema) {
            console.error(`The schema of node item '${id}' could not be found.`);
            return;
        }

        if (!schema.isDeletable) {
            console.error(`This schema cannot be deleted.`);
            return;
        }

        // Delete the node connections to other nodes too
        if (NodeConnections.deleteAllConnections(id)) {
            scriplet.deleteNode(id);
            this.updateDOM();
        }
    }

    // Should only happen on drag end, but node connections won't follow anymore if not done on drag move
    // TODO: Optimize this!
    handleDragNode(itemId: number, x: number, y: number) {
        const scriplet = ScripletManager.currentScriplet;
        if (!scriplet) {
            console.error(`The current scriplet of id '${this.props.scripletId}' could not be found.`);
            return;
        }

        const node = scriplet.getNode(itemId);
        if (!node) {
            console.error(`The node of id '${itemId}' could not be found.`);
            return;
        }

        node.x = x;
        node.y = y;

        scriplet.items.set(itemId, node);

        // TODO: This lags! If possible, access the edges (konva path) directly and change the coords there
        this.updateDOM();
    }

    handleDeleteEdge(inputItemId: number, inputSlotId: number, outputItemId: number, outputSlotId: number) {
        NodeConnections.deleteConnection(inputItemId, inputSlotId, outputItemId, outputSlotId);
        this.updateDOM();
    }

    handleDeleteFlowEdge(inputItemId: number, inputSlotId: number, outputItemId: number, outputSlotId: number) {
        NodeConnections.deleteFlowConnection(inputItemId, inputSlotId, outputItemId, outputSlotId);
        this.updateDOM();
    }

    handleZoom(event: KonvaEventObject<WheelEvent>) {
        event.evt.preventDefault();
        const stage = event.target.getStage();
        if (!stage) {
            console.error(`The Konva Stage could not be found.`);
            return;
        }
            
        const oldScale: number = stage.scaleX();
        const scaleBy = 1.1;
        const pointer = stage.getPointerPosition();
        if (!pointer) {
            console.error(`The Konva pointer position could not be found.`);
            return;
        }

        const mousePosition = {
            x: (pointer.x - stage.x()) / oldScale,
            y: (pointer.y - stage.y()) / oldScale
        };

        const newScale = event.evt.deltaY < 0 ? oldScale * scaleBy : oldScale / scaleBy;

        stage.scale({ x: newScale, y: newScale});

        const newPos = {
            x: pointer.x - mousePosition.x * newScale,
            y: pointer.y - mousePosition.y * newScale
        };
        stage.position(newPos);
        stage.batchDraw();
    }

    handleContextMenu(event: KonvaEventObject<PointerEvent>) {
        if (event.target.getType() !== "Stage")
            return;
        const stage = event.target.getStage();
        const layer: Konva.Layer | null = event.target.getChildren(item => item.getAttr("data-name") === "context-menu-layer")[0];
        if (!layer || !stage) {
            console.error(`Either the Konva layer or Konva stage could not be found.`);
            return;
        }

        const pointerPos = stage.getPointerPosition();
        if (!pointerPos) {
            console.error(`The Konva pointer position could not be found.`);
            return;
        }

        pointerPos.x = (pointerPos.x - stage.x()) / stage.scaleX();
        pointerPos.y = (pointerPos.y - stage.y()) / stage.scaleY();

        this.setState({isContextMenuShown: true, contextMenuPos: {x: event.evt.pageX, y: event.evt.pageY}, nodeInitPos: pointerPos});
    }

    handleCancelContextMenu() {
        this.setState({isContextMenuShown: false});
    }

    fitToParent() {
        const container = this.stageContainer.current;
        const stage = this.stage.current;
        if (!container || !stage) {
            console.error(`Either the referenced container or referenced stage could not be found.`);
            return;
        }
        
        const stageObj = stage.getStage();
        stageObj.width(container.offsetWidth);
        stageObj.height(container.offsetHeight);
        stageObj.draw();
    }
    
    render() {
        const { isReadyToMount, isReady, isContextMenuShown, contextMenuPos } = this.state;

        if (!isReadyToMount && !isReady)
            return null;

        const rowHeight = 40;
        const topBarHeight = 30;
        const scriplet = ScripletManager.currentScriplet;
        if (!scriplet) {
            console.error(`The current scriplet of id '${this.props.scripletId}' could not be found.`);
            return null;
        }

        return(
            <div ref={this.stageContainer} className="main">
                <PortalStage ref={this.stage}
                    className="bg-gray-200"
                    onWheel={(event: KonvaEventObject<WheelEvent>) => {
                        this.handleZoom(event);
                        this.handleCancelContextMenu();
                        KonvaForm.cancel();
                    }}
                    onContextMenu={(event: KonvaEventObject<PointerEvent>) => {
                        event.evt.preventDefault();
                        event.cancelBubble = true;

                        this.handleContextMenu(event);
                    }}
                    onClick={(event: KonvaEventObject<MouseEvent>) => {
                        this.handleCancelContextMenu();
                        KonvaForm.cancel();
                    }}
                    onDragStart={(event: KonvaEventObject<DragEvent>)  => {
                        this.handleCancelContextMenu();
                        KonvaForm.cancel();
                    }}
                    draggable>
                    <PortalLayer id="edges-layer">
                    </PortalLayer>
                    <Layer>
                        {[...scriplet.getNodes()].map(item => {
                        const schema = item.schema;
                        if (!schema) {
                            console.error(`The schema for node item '${item.id}' could not be found.`);
                            return null;
                        }
                        const inputSlots = NodeConnections.getInputConnections(item.id);
                        const inputFlowSlots = NodeConnections.getInputFlowConnections(item.id);
                        return (
                            <Group key={`node-${item.id}`}>
                                {inputSlots.map(conn => {
                                if (!conn)
                                    return null;
                                const otherItem = scriplet.getNode(conn.itemId);
                                if (!otherItem) {
                                    console.error(`The node item of id '${conn.itemId}' could not be found.`);
                                    return null;
                                }
                                const otherSchema = otherItem.schema;
                                if (!otherSchema) {
                                    console.error(`The schema for node item '${otherItem.id}' could not be found.`);
                                    return null;
                                }
                                return(
                                    <Portal key={`edge-${item.id}-${conn.id}-${conn.itemId}-${conn.slotId}`} 
                                            containerId="edges-layer">
                                        <Edge 
                                            startPos={{x: otherItem.x, y: otherItem.y + (conn.slotId * rowHeight) + topBarHeight}}
                                            endPos={{x: item.x, y: item.y + (conn.id * rowHeight) + topBarHeight}}
                                            nodeWidthOther={otherSchema.width}
                                            nodeHeight={rowHeight}
                                            handleOnClick={() => this.handleDeleteEdge(item.id, conn.id, conn.itemId, conn.slotId)}>
                                            {(midpointPos, isHovered) => (
                                                <>
                                                    <Rect visible={isHovered}
                                                        x={midpointPos.x - 12}
                                                        y={midpointPos.y - 10}
                                                        width={24}
                                                        height={16}
                                                        fill={Colors.Gray[800]}
                                                        listening={false} />
                                                    <Text visible={isHovered}
                                                        x={midpointPos.x - 12}
                                                        y={midpointPos.y - 10}
                                                        width={24}
                                                        height={19}
                                                        text="&#215;"
                                                        fontStyle="bold"
                                                        fontSize={20}
                                                        align="center"
                                                        verticalAlign="middle"
                                                        fill={Colors.Gray[100]}
                                                        listening={false} />
                                                </>
                                            )}
                                        </Edge>
                                    </Portal>
                                );
                                })}
                                {inputFlowSlots.map(conn => {
                                if (!conn)
                                    return null;
                                const otherItem = scriplet.getNode(conn.itemId);
                                if (!otherItem) {
                                    console.error(`The item of id '${conn.itemId}' could not be found.`);
                                    return null;
                                }
                                const otherSchema = otherItem.schema;
                                if (!otherSchema) {
                                    console.error(`The schema for node item '${otherItem.id}' could not be found.`);
                                    return null;
                                }
                                return(
                                    <Portal key={`edge-flow-${item.id}-${conn.id}-${conn.itemId}-${conn.slotId}`} 
                                            containerId="edges-layer">
                                        <Edge 
                                            startPos={{x: otherItem.x, y: otherItem.y + (conn.slotId * rowHeight) + topBarHeight}}
                                            endPos={{x: item.x, y: item.y + (conn.id * rowHeight) + topBarHeight}}
                                            nodeWidthOther={otherSchema.width}
                                            nodeHeight={rowHeight}
                                            handleOnClick={() => this.handleDeleteFlowEdge(item.id, conn.id, conn.itemId, conn.slotId)}>
                                            {(midpointPos, isHovered) => (
                                                <>
                                                    <Rect visible={isHovered}
                                                        x={midpointPos.x - 12}
                                                        y={midpointPos.y - 10}
                                                        width={24}
                                                        height={16}
                                                        fill={Colors.Gray[800]}
                                                        listening={false} />
                                                    <Text visible={isHovered}
                                                        x={midpointPos.x - 12}
                                                        y={midpointPos.y - 10}
                                                        width={24}
                                                        height={19}
                                                        text="&#215;"
                                                        fontStyle="bold"
                                                        fontSize={20}
                                                        align="center"
                                                        verticalAlign="middle"
                                                        fill={Colors.Gray[100]}
                                                        listening={false} />
                                                </>
                                            )}
                                        </Edge>
                                    </Portal>
                                );
                                })}
                                <Node item={item}
                                    width={schema.width}
                                    rowHeight={rowHeight}
                                    topBarHeight={topBarHeight}
                                    handleDragNode={this.handleDragNode}
                                    deleteNodeItem={this.deleteNodeItem}
                                    updateDOM={this.updateDOM} />
                            </Group>
                        );
                        })}
                    </Layer>
                    <Layer data-name="context-menu-layer">
                    </Layer>
                </PortalStage>
                <SchemaSelector isVisible={isContextMenuShown} x={contextMenuPos.x} y={contextMenuPos.y} addNodeItem={this.addNodeItem} />
            </div>
        );
    }
}