import React from 'react';
import { Rect, Group, Label, Tag, Text } from 'react-konva';
import { Group as KonvaGroup } from 'konva/types/Group';
import Colors from '../utils/Colors';
import NodeConnections from '../managers/NodeConnections';
import { Input, KonvaForm } from '../components/KonvaForm';
import { Path } from 'konva/types/shapes/Path';
import { NodeItem } from '../models/NodeItem';
import { InputSlot, OutputSlot, InputFlowSlot, OutputFlowSlot } from '../models/NodeSlot';
import { InputSlotDOM, OutputSlotDOM, InputFlowSlotDOM, OutputFlowSlotDOM } from './NodeSlot';
import { KonvaUtils } from '../utils/KonvaUtils';
import '../assets/css/fontawesome.min.css';
import '../assets/css/solid.min.css';
import ScripletManager from '../managers/ScripletManager';

export type NodeProps = {
    item: NodeItem;
    width: number;
    rowHeight: number;
    topBarHeight: number;
    handleDragNode(index: number, x: number, y: number): any;
    deleteNodeItem(id: number): any;
    updateDOM(): any;
}

export type NodeState = {
    draggedHandlePath: Path | null;
    isStateOpen: boolean;
    stateVisual: KonvaGroup | null;
}

export default class Node extends React.Component<NodeProps, NodeState> {
    private group: React.RefObject<KonvaGroup>;
    
    constructor(props: NodeProps) {
        super(props);

        this.group = React.createRef<KonvaGroup>();

        this.state = {
            draggedHandlePath: null,
            isStateOpen: false,
            stateVisual: null
        }

        /*
        this.handleHoverEnterEditableText = this.handleHoverEnterEditableText.bind(this);
        this.handleHoverExitEditableText = this.handleHoverExitEditableText.bind(this);
        this.handleHoverEnterToggleState = this.handleHoverEnterToggleState.bind(this);
        this.handleHoverExitToggleState = this.handleHoverExitToggleState.bind(this);
        this.openState = this.openState.bind(this);
        this.closeState = this.closeState.bind(this);
        this.toggleState = this.toggleState.bind(this);
        */
    }

    componentDidMount() {
        const layer = this.group.current?.getLayer();
        if (layer) {
        }
    }

    // These commented code below can eventually be used if there are some node schemas
    // in the future that can have some state saved beyond their inputs.
    /* 
    handleHoverEnterToggleState(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "pointer");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const textBG = event.currentTarget.children[0];
        textBG.setAttr("fill", Colors.Gray[700]);
        layer.draw();
    }

    handleHoverExitToggleState(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "default");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const textBG = event.currentTarget.children[0];
        textBG.setAttr("fill", Colors.Gray[900]);
        layer.draw();
    }

    openState() {
        if (this.state.stateVisual) {
            console.error(`This node already has an opened state visual. Close it first then try again.`);
            return;
        }

        const { item, rowHeight, topBarHeight } = this.props;

        // Show the custom schema state visual here
        const schema = item.schema;
        if (!schema) {
            console.error(`The node schema could not be found.`);
            return;
        }

        const state = schema.createNewSchema();

        const stateVisual = schema.state.onShowStateVisual(this.props);
        if (!stateVisual) {
            console.error(`The Konva group to show the node state could not be found.`);
            return;
        }

        const thisGroup = this.group.current;
        if (!thisGroup) {
            console.error(`The Konva group for this node could not be found.`);
            return;
        }

        const layer = thisGroup.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const bgRect = this.group.current?.children[0] as Konva.Rect;
        if (!bgRect) {
            console.error(`The Konva bg rect could not be found.`);
            return;
        }

        bgRect.cornerRadius([5, 5, 0, 0]);

        const height = schema.maxLength() * rowHeight + topBarHeight + 10;
        stateVisual.x(0);
        stateVisual.y(height);
        thisGroup.add(stateVisual);
        layer.draw();

        this.setState({isStateOpen: true, stateVisual: stateVisual});
    }

    closeState() {
        if (!this.state.stateVisual) {
            console.error(`This node has no state visual to close.`);
            return;
        }

        const bgRect = this.group.current?.children[0] as Konva.Rect;
        if (!bgRect) {
            console.error(`The Konva bg rect could not be found.`);
            return;
        }

        bgRect.cornerRadius(5);

        this.state.stateVisual.destroy();
        this.setState({isStateOpen: false, stateVisual: null});
    }

    toggleState() {
        if (this.state.isStateOpen)
            this.closeState();
        else
            this.openState();
    }
    */


    render() {
        const { item, width, rowHeight, topBarHeight, handleDragNode, deleteNodeItem, updateDOM } = this.props;
        const height = item.schema ? item.schema.maxLength() * rowHeight + topBarHeight : 0;
        
        const schema = item.schema;
        if (!schema) {
            console.error(`The schema for node item '${item.id}' could not be found.`);
            return null;
        }

        const inputSlots: InputSlot[] = schema.inputSlots;
        const outputSlots: OutputSlot[] = schema.outputSlots;
        const inputFlowSlots: InputFlowSlot[] = schema.inputFlowSlots;
        const outputFlowSlots: OutputFlowSlot[] = schema.outputFlowSlots;

        let inputs: (InputSlot | InputFlowSlot)[] = [];
        inputs = inputs.concat(inputFlowSlots, inputSlots);

        let outputs: (OutputSlot | OutputFlowSlot)[] = [];
        outputs = outputs.concat(outputFlowSlots, outputSlots);

        // TODO: These two seem hackish
        const inputTypeGuard = (input: InputSlot | InputFlowSlot): input is InputSlot => {
            if ((input as InputSlot).type)
                return true;
            return false;
        }
        const outputTypeGuard = (output: OutputSlot | OutputFlowSlot): output is OutputSlot => {
            if ((output as OutputSlot).type)
                return true;
            return false;
        }
        return(
            <Group id={`node-${item.id}`}
                    ref={this.group}
                    x={item.x}
                    y={item.y}
                    width={width}
                    height={height}
                    draggable
                    onDragStart={event => {
                        KonvaUtils.setCursor(event, "move");
                        KonvaForm.cancel();
                    }}
                    onDragEnd={event => {
                        KonvaUtils.setCursor(event, "default");
                        ScripletManager.currentScriplet?.updateNodes();
                    }}
                    onDragMove={event => handleDragNode(item.id, event.target.x(), event.target.y())}>
                <Rect name="background"
                        x={0}
                        y={0}
                        width={width}
                        height={height + 10}
                        fill={Colors.Gray[900]}
                        cornerRadius={5} />
                <Rect name="top-bar"
                        x={0}
                        y={0}
                        width={width}
                        height={topBarHeight}
                        fill={Colors.Gray[900]}
                        cornerRadius={[5, 5, 0, 0]} />
                <Input x={10}
                        y={2.5}
                        width={schema.isDeletable ? width - 40 : width - 20}
                        height={topBarHeight - 5}
                        borderRadius={2}
                        padding={6}
                        textColor={Colors.Gray[100]}
                        bgColor={Colors.Gray[900]}
                        hoverColor={Colors.Gray[800]}
                        value={(!item.customName || item.customName === "") ? item.schema?.name ?? "undefined" : item.customName}
                        onChange={value => {
                            item.customName = value; 
                            updateDOM();
                            return true;
                        }} />
                {(schema.isDeletable && 
                    <Label x={width - 20}
                            y={8}
                            onMouseEnter={event => {
                                event.cancelBubble = true;
                                KonvaUtils.setCursor(event, "pointer");
                            }}
                            onMouseLeave={event => {
                                event.cancelBubble = true;
                                KonvaUtils.setCursor(event, "default");
                            }}
                            onClick={event => {
                                KonvaUtils.setCursor(event, "default");
                                KonvaForm.cancel();
                                deleteNodeItem(item.id);
                            }}>
                        <Tag fill={Colors.Red[500]}
                            cornerRadius={15} />
                        <Text width={12}
                                height={12}
                                fontSize={14}
                                fontStyle="bold"
                                align="center"
                                verticalAlign="center"
                                fill={Colors.Gray[900]}
                                text="&#215;" />
                    </Label>
                )}
                <Rect x={0}
                        y={topBarHeight}
                        width={width}
                        height={rowHeight * (item.schema?.maxLength() ?? 1)}
                        fill={Colors.Gray[800]} />
                {Array.from(Array(item.schema?.maxLength() ?? 0)).map((it, i) => {
                const input: InputSlot | undefined = i < inputs.length && inputTypeGuard(inputs[i]) ? inputs[i] as InputSlot : undefined;
                const output: OutputSlot | undefined = i < outputs.length && outputTypeGuard(outputs[i]) ? outputs[i] as OutputSlot : undefined;

                if (input && input.name == null)
                    input.name = `Input${i}`;
                if (output && output.name == null)
                    output.name = `Output${i}`;

                const inputFlow: InputFlowSlot | undefined = i < inputs.length && !inputTypeGuard(inputs[i]) ? inputs[i] as InputFlowSlot : undefined;
                const outputFlow: OutputFlowSlot | undefined = i < outputs.length && !outputTypeGuard(outputs[i]) ? outputs[i] as OutputFlowSlot : undefined;
                
                if (inputFlow && inputFlow.name == null)
                    inputFlow.name = `Flow${i}`;
                if (outputFlow && outputFlow.name == null)
                    outputFlow.name = `Flow${i}`;
                return(
                    <React.Fragment key={`node-slot-${item.id}-${i}`}>
                        {input != null  && 
                            <>
                            <InputSlotDOM inputSlot={input}
                                        inputConnection={item.getInputConnection(input.id)}
                                        x={0}
                                        y={topBarHeight + (i * rowHeight)}
                                        itemId={item.id}
                                        onInputChange={value => {
                                            item.setInputValue(input.id, value);
                                            ScripletManager.currentScriplet?.updateNodes();

                                            return true;
                                        }}
                                        onConnect={(otherItemId, otherSlotId) => {
                                            NodeConnections.addConnection(item.id, input.id, otherItemId, otherSlotId);
                                            updateDOM();
                                        }} />
                            </>
                        }
                        {output != null &&
                            <>
                            <OutputSlotDOM outputSlot={output}
                                        outputConnections={item.getOutputConnections(output.id)}
                                        x={width}
                                        y={topBarHeight + (i * rowHeight)}
                                        itemId={item.id}
                                        onConnect={(otherItemId, otherSlotId) => {
                                            NodeConnections.addConnection(otherItemId, otherSlotId, item.id, output.id);
                                            updateDOM();
                                        }} />
                            </>
                        }
                        {inputFlow != null  && 
                            <>
                            <InputFlowSlotDOM inputFlowSlot={inputFlow}
                                        inputFlowConnection={item.getInputFlowConnection(inputFlow.id)}
                                        x={0}
                                        y={topBarHeight + (i * rowHeight)}
                                        itemId={item.id}
                                        onConnect={(otherItemId, otherSlotId) => {
                                            NodeConnections.addFlowConnection(item.id, inputFlow.id, otherItemId, otherSlotId);
                                            updateDOM();
                                        }} />
                            </>
                        }
                        {outputFlow != null &&
                            <>
                            <OutputFlowSlotDOM outputFlowSlot={outputFlow}
                                        outputFlowConnection={item.getOutputFlowConnection(outputFlow.id)}
                                        x={width}
                                        y={topBarHeight + (i * rowHeight)}
                                        itemId={item.id}
                                        onConnect={(otherItemId, otherSlotId) => {
                                            NodeConnections.addFlowConnection(otherItemId, otherSlotId, item.id, outputFlow.id);
                                            updateDOM();
                                        }} />
                            </>
                        }
                    </React.Fragment>
                );
                })}
                {/*
                <Label name="show-state"
                        x={0}
                        y={height}
                        width={width}
                        height={5}
                        onMouseEnter={this.handleHoverEnterToggleState}
                        onMouseLeave={this.handleHoverExitToggleState}
                        onClick={this.toggleState}>
                    <Tag fill={Colors.Gray[900]}
                            cornerRadius={5} />
                    <Text width={width}
                            height={10}
                            fontFamily="Font Awesome\ 5 Free"
                            text={`${this.state.isStateOpen ? "\uf106" : "\uf107"}`}
                            fill={Colors.Gray[100]}
                            align="center"
                            verticalAlign="middle" />
                </Label>
                */}
            </Group>
        );
    }
}