import React from 'react';
import { Vector2d } from 'konva/types/types';
import { Path } from 'react-konva';
import Colors from '../utils/Colors';

export type EdgeProps = {
    startPos: Vector2d;
    endPos: Vector2d;
    nodeWidthOther: number;
    nodeHeight: number;
    handleOnClick?(): any;
    children?(midpointPos: Vector2d, isHovered: boolean): React.ReactNode | null;
}

export type EdgeState = {
    isHovered: boolean;
}

export default class Edge extends React.Component<EdgeProps, EdgeState> {
    constructor(props: EdgeProps) {
        super(props);

        this.state = {
            isHovered: false
        };

        this.handleOnEnter = this.handleOnEnter.bind(this);
        this.handleOnExit = this.handleOnExit.bind(this);
    }

    handleOnEnter() {
        this.setState({isHovered: true});
    }

    handleOnExit() {
        this.setState({isHovered: false});
    }

    render() {
        const { startPos, endPos, nodeWidthOther: nodeWidthOutput, nodeHeight, handleOnClick, children } = this.props;
        const { isHovered } = this.state;
        const halfNodeHeight = nodeHeight / 2;
        const midpointPos: Vector2d = {
            x: ((endPos.x - startPos.x - nodeWidthOutput) / 2) + (startPos.x + nodeWidthOutput),
            y: ((endPos.y + halfNodeHeight - startPos.y - halfNodeHeight) / 2) + (startPos.y + halfNodeHeight)
        }
        const pathData = `M${startPos.x + nodeWidthOutput} ${startPos.y + halfNodeHeight - 2} Q${startPos.x + nodeWidthOutput + 30} ${startPos.y + halfNodeHeight - 2} ${midpointPos.x} ${midpointPos.y - 2} Q${endPos.x - 30} ${endPos.y + halfNodeHeight - 2} ${endPos.x} ${endPos.y + halfNodeHeight - 2}`;
        return(
            <>
                <Path data={pathData}
                    stroke={Colors.Gray[800]}
                    strokeWidth={4} />
                <Path data={pathData}
                    className="cursor-pointer"
                    stroke="transparent"
                    strokeWidth={50}
                    onMouseEnter={event => {
                        this.handleOnEnter();
                        const stage = event.target.getStage();
                        if (!stage) {
                            console.error(`The Konva stage could not be found.`);
                            return;
                        }
                        stage.container().style.cursor = "pointer";
                    }}
                    onMouseLeave={event => {
                        this.handleOnExit();
                        const stage = event.target.getStage();
                        if (!stage) {
                            console.error(`The Konva stage could not be found.`);
                            return;
                        }
                        stage.container().style.cursor = "default";
                    }}
                    onMouseDown={handleOnClick}
                    onTouchEnd={handleOnClick} />
                {children != null ? children(midpointPos, isHovered) : null}
            </>
        );
    }
}