import { BaseNodeSchema } from './NodeSchema';
import NodeSchemaManager from '../managers/NodeSchemaManager';
import VariableManager from '../managers/VariableManager';
import { IdValuePair, ItemSlotPair } from './NodeSlot';

// TODO: Implement slot connections here instead because...
// When getting a node from the scriplet, it always creates a new schema,
// therefore the slot connections added there when connecting nodes are not saved.
export interface INodeItem {
    id: number;
    customName?: string;
    x: number;
    y: number;
    schemaId: string;
    inputValues: IdValuePair[];
    inputSlots: ItemSlotPair[];
    inputFlowSlots: ItemSlotPair[];
    outputSlots: Map<number, ItemSlotPair[]>;
    outputFlowSlots: ItemSlotPair[];
}

// TODO: When position changes, new x and y coords are not saved to db
export class NodeItem implements INodeItem {
    public id: number;
    public customName?: string;
    public x: number;
    public y: number;
    public schemaId: string;
    public inputValues: IdValuePair[];
    public inputSlots: ItemSlotPair[];
    public inputFlowSlots: ItemSlotPair[];
    public outputSlots: Map<number, ItemSlotPair[]>;
    public outputFlowSlots: ItemSlotPair[];

    public variableName: string;
    public schema: BaseNodeSchema;

    constructor(id: number, x: number, y: number, schemaId: string, inputValues: IdValuePair[], inputSlots: ItemSlotPair[], inputFlowSlots: ItemSlotPair[], outputSlots: Map<number, ItemSlotPair[]>, outputFlowSlots: ItemSlotPair[], customName?: string) {
        this.id = id;
        this.x = x;
        this.y = y;
        this.schemaId = schemaId;
        this.inputValues = inputValues;
        this.inputSlots = inputSlots;
        this.inputFlowSlots = inputFlowSlots;
        this.outputSlots = outputSlots;
        this.outputFlowSlots = outputFlowSlots;
        
        if (customName)
            this.customName = customName;

        this.variableName = VariableManager.getNextVariableName(schemaId) ?? "undefined";
        this.schema = NodeSchemaManager.getNewSchema(id, schemaId);

        // Necessary so the variableName and schema are not serialized to db
        Object.defineProperties(this, {
            variableName: {enumerable: false, writable: true},
            schema: {enumerable: false, writable: true}
        });
    }

    getInputConnection(slotId: number): ItemSlotPair | undefined {
        const result = this.inputSlots.filter(slot => slot.id === slotId);
        if (result.length > 1)
            console.error(`Input slot '${slotId}' of node item '${this.id}' can only have 1 connection. Will return the first connection found.`);

        if (result.length < 1)
            return;
        
        return result[0];
    }

    setInputConnection(id: number, itemId: number, slotId: number) {
        // TODO: Save changes to db
        const result = this.inputSlots.filter(slot => slot.id === id);
        if (result.length > 1)
            console.error(`Input slot '${id}' of node item '${this.id}' can only have 1 connection. Will replace the first connection found.`);

        if (result.length < 1) {
            this.inputSlots.push({id, itemId, slotId});
            return;
        }
        
        const slot = result[0];
        slot.itemId = itemId;
        slot.slotId = slotId;
    }

    deleteInputConnection(slotId: number) {
        this.inputSlots = this.inputSlots.filter(pair => pair.id !== slotId);
    }

    // I'm an idiot... I really made just one field to store the input
    // value of a node when a node can have multiple input slots... *facepalm*
    getInputValue(slotId: number): IdValuePair | undefined {
        const result = this.inputValues.filter(value => value.id === slotId);
        if (result.length > 1)
            console.error(`Input value '${slotId}' of node item '${this.id}' can only have 1 value. Will return the first value found.`);

        if (result.length < 1)
            return;
        
        return result[0].value;
    }

    setInputValue(slotId: number, value?: any) {
        const result = this.inputValues.filter(value => value.id === slotId);
        if (result.length > 1)
            console.error(`Input value '${slotId}' of node item '${this.id}' can only have 1 value. Will return the first value found.`);

        if (result.length < 1) {
            this.inputValues.push({id: slotId, value: value});
            return;
        }

        const val = result[0];
        val.value = value;
    }

    getOutputConnections(slotId: number): ItemSlotPair[] {
        // TODO: Also check if all slots in a subset have the same ids
        const result = this.outputSlots.get(slotId);
        if (!result)
            return [];

        return result;
    }

    setOutputConnection(id: number, itemId: number, slotId: number) {
        const result = this.outputSlots.get(id);
        if (!result) {
            this.outputSlots.set(id, [{id, itemId, slotId}]);
            return;
        }

        const similar = result.find(pair => pair.id === id && pair.itemId === itemId && pair.slotId === slotId);
        if (similar) {
            console.error(`The connection '${itemId}, ${slotId}' is already connected to node item slot '${this.id}, ${id}'. Something in the code may have caused this duplication.`);
            return;
        }

        result.push({id, itemId, slotId});
    }

    deleteOutputConnection(id: number, itemId: number, slotId: number) {
        let result = this.outputSlots.get(id);
        if (result) {
            result = result.filter(pair => pair.id !== id && pair.itemId !== itemId && pair.slotId !== slotId);
            this.outputSlots.set(id, result);
        }
    }

    getInputFlowConnection(slotId: number): ItemSlotPair | undefined {
        const result = this.inputFlowSlots.filter(slot => slot.id === slotId);
        if (result.length > 1)
            console.error(`Input flow slot '${slotId}' of node item '${this.id}' can only have 1 connection. Will return the first connection found.`);

        if (result.length < 1)
            return;
        
        return result[0];
    }

    setInputFlowConnection(id: number, itemId: number, slotId: number) {
        // TODO: Save changes to db
        const result = this.inputFlowSlots.filter(slot => slot.id === id);
        if (result.length > 1)
            console.error(`Input flow slot '${id}' of node item '${this.id}' can only have 1 connection. Will replace the first connection found.`);

        if (result.length < 1) {
            this.inputFlowSlots.push({id, itemId, slotId});
            return;
        }
        
        const slot = result[0];
        slot.itemId = itemId;
        slot.slotId = slotId;
    }

    deleteInputFlowConnection(slotId: number) {
        this.inputFlowSlots = this.inputFlowSlots.filter(pair => pair.id !== slotId);
    }

    getOutputFlowConnection(slotId: number): ItemSlotPair | undefined {
        const result = this.outputFlowSlots.filter(slot => slot.id === slotId);
        if (result.length > 1)
            console.error(`Output flow slot '${slotId}' of node item '${this.id}' can only have 1 connection. Will return the first connection found.`);

        if (result.length < 1)
            return;
        
        return result[0];
    }

    setOutputFlowConnection(id: number, itemId: number, slotId: number) {
        // TODO: Save changes to db
        const result = this.outputFlowSlots.filter(slot => slot.id === id);
        if (result.length > 1)
            console.error(`Output flow slot '${id}' of node item '${this.id}' can only have 1 connection. Will replace the first connection found.`);

        if (result.length < 1) {
            this.outputFlowSlots.push({id, itemId, slotId});
            return;
        }
        
        const slot = result[0];
        slot.itemId = itemId;
        slot.slotId = slotId;
    }

    deleteOutputFlowConnection(slotId: number) {
        this.outputFlowSlots = this.outputFlowSlots.filter(pair => pair.id !== slotId);
    }
}