import { OutputSlot, InputSlot, InputFlowSlot, OutputFlowSlot } from "./NodeSlot";
import { Number, Boolean } from "../utils/Types";
import NodeSchemaManager from "../managers/NodeSchemaManager";

function AddSchema(target: Function) {
    const origCtor = target;

    function instantiate(constructor: any, ...args: any[]) {
        return new constructor(...args);
    }

    const newCtor = function(...args: any[]) {
        return instantiate(origCtor, ...args);
    };

    newCtor.prototype = origCtor.prototype;

    const schema = newCtor() as BaseNodeSchema;
    NodeSchemaManager.nodeSchemas.set(schema.id, schema.createNew(-1));
}

export enum BaseSchemaTypes {
    Start = "Hidden/Start",
    IfElse = "Base/ControlFlow/IfElse",
    ForLoop = "Base/ControlFlow/ForLoop",
    WhileLoop = "Base/ControlFlow/WhileLoop",
    Input = "Hidden/Input",
    Output = "Hidden/Output",
    PrintNumber = "Base/Debug/PrintNumber",
    PrintBoolean = "Base/Debug/PrintBoolean",
    AddNumbers = "Base/Math/Number/Add",
    SubtractNumbers = "Base/Math/Number/Subtract",
    MultiplyNumbers = "Base/Math/Number/Multiply",
    DivideNumbers = "Base/Math/Number/Divide",
    ModuloNumbers = "Base/Math/Number/Modulo",
    LessThanNumbers = "Base/Math/Number/LessThan",
    LessThanEqualNumbers = "Base/Math/Number/LessThanEqual",
    GreaterThanNumbers = "Base/Math/Number/GreaterThan",
    GreaterThanEqualNumbers = "Base/Math/Number/GreaterThanEqual"
}

// TODO: Possibly move all these things to a config file that is read by these and behavior is filled in
export abstract class BaseNodeSchema {
    readonly id: string;
    readonly name: string;
    readonly uniqueNamePrefix: string;
    readonly width: number;
    //readonly hasVariable: boolean;
    readonly inputFlowSlots: InputFlowSlot[];
    readonly outputFlowSlots: OutputFlowSlot[];
    readonly inputSlots: InputSlot[];
    readonly outputSlots: OutputSlot[];

    readonly isDeletable: boolean;

    itemId: number = -1;

    protected abstract createNewSchema(): BaseNodeSchema;

    constructor(id: string, name: string, uniqueNamePrefix: string, width: number, inputFlowSlots: InputFlowSlot[], outputFlowSlots: OutputFlowSlot[], inputSlots: InputSlot[], outputSlots: OutputSlot[], isDeletable: boolean = true) {
        this.id = id;
        this.name = name;
        this.uniqueNamePrefix = uniqueNamePrefix;
        this.width = width;
        this.inputFlowSlots = inputFlowSlots;
        this.outputFlowSlots = outputFlowSlots;
        this.inputSlots = inputSlots;
        this.outputSlots = outputSlots;

        this.isDeletable = isDeletable;
    }

    maxLength(): number {
        return Math.max(this.inputFlowSlots.length + this.inputSlots.length, this.outputFlowSlots.length + this.outputSlots.length);
    }

    createNew(itemId: number): BaseNodeSchema {
        const schema = this.createNewSchema();
        schema.itemId = itemId;
        return schema;
    }
}

@AddSchema
export class StartFlowSchema extends BaseNodeSchema {
    constructor() {
        const id = BaseSchemaTypes.Start;
        const name = "Start";
        const uniqueNamePrefix = "start";

        const output1 = new OutputFlowSlot(0, "Body");
        const outputFlowSlots = [output1];
        const inputFlowSlots: InputFlowSlot[] = [new InputFlowSlot(0, () => {
            output1.goNext(this.itemId);
        }, false, " ")];

        super(id, name, uniqueNamePrefix, 120, inputFlowSlots, outputFlowSlots, [], [], false);
    }

    createNewSchema(): StartFlowSchema {
        return new StartFlowSchema();
    }
}

@AddSchema
export class IfElseSchema extends BaseNodeSchema {
    constructor() {
        const id = BaseSchemaTypes.IfElse;
        const name = "If/Else";
        const uniqueNamePrefix = "ifElse";

        const input1 = new InputSlot(1, Boolean.className, Boolean.defaultValue, true, "Condition");
        const inputSlots = [input1];

        const output1 = new OutputFlowSlot(0, "True");
        const output2 = new OutputFlowSlot(1, "False");
        const outputFlowSlots = [output1, output2];

        const inputFlowSlots = [new InputFlowSlot(0, (thisSlot, returnToSlot) => {
            if (input1.getValue(this.itemId) === true)
                output1.goNext(this.itemId, returnToSlot)
            else
                output2.goNext(this.itemId, returnToSlot);
        }, true, "Entry")];

        super(id, name, uniqueNamePrefix, 220, inputFlowSlots, outputFlowSlots, inputSlots, []);
    }

    createNewSchema(): IfElseSchema {
        return new IfElseSchema();
    }
}

@AddSchema
export class ForLoopSchema extends BaseNodeSchema {
    private start: number | undefined;
    private end: number | undefined;
    private increment: number | undefined;
    private index: number;

    constructor() {
        const id = BaseSchemaTypes.ForLoop;
        const name = "For Loop";
        const uniqueNamePrefix = "forLoop";

        const outputFlow1 = new OutputFlowSlot(0, "Body");
        const outputFlow2 = new OutputFlowSlot(1, "Exit");
        const outputFlowSlots = [outputFlow1, outputFlow2];

        const input2 = new InputSlot(1, Number.className, Number.defaultValue, true, "Start");
        const input3 = new InputSlot(2, Number.className, 10, true, "End");
        const input4 = new InputSlot(3, Number.className, 1, true, "Increment");
        const inputSlots = [input2, input3, input4];

        const output3 = new OutputSlot(2, Number.className, () => {
            // TODO: Kinda hackish to solve the problem below
            return this.index - (this.increment ?? 1);
        }, "Index");
        const outputSlots = [output3];

        const inputFlowSlots = [new InputFlowSlot(0, (thisSlot, returnToSlot) => {
            if (!thisSlot.returnToSlot)
                thisSlot.returnToSlot = returnToSlot;
            if (this.start == null || this.end == null || this.increment == null) {
                this.start = input2.getValue(this.itemId);
                this.end = input3.getValue(this.itemId);
                this.increment = input4.getValue(this.itemId);
                this.index = this.start ?? 0;
            }
            
            if (this.index < (this.end ?? 0)) {
                // TODO: This should really increment only after everything else is done.
                this.index += this.increment ?? 1;
                outputFlow1.goNext(this.itemId, {id: -1, itemId: this.itemId, slotId: thisSlot.id});
            } else {
                const returnTo = thisSlot.returnToSlot;
                thisSlot.returnToSlot = undefined;
                this.index = 0;
                this.start = undefined;
                this.end = undefined;
                outputFlow2.goNext(this.itemId, returnTo);
            }
        }, true, "Entry")];

        super(id, name, uniqueNamePrefix, 200, inputFlowSlots, outputFlowSlots, inputSlots, outputSlots);
        this.index = 0;
    }

    createNewSchema(): ForLoopSchema {
        return new ForLoopSchema();
    }
}

@AddSchema
export class WhileLoopSchema extends BaseNodeSchema {
    // This limit is so the while loop will still halt at some point instead of 
    // freezing the entire app. Can be set to 0 to run forever.
    private limit: number | undefined;
    private counter: number;
    constructor() {
        const id = BaseSchemaTypes.WhileLoop;
        const name = "While Loop";
        const uniqueNamePrefix = "whileLoop";

        const outputFlow1 = new OutputFlowSlot(0, "Body");
        const outputFlow2 = new OutputFlowSlot(1, "Exit");
        const outputFlowSlots = [outputFlow1, outputFlow2];

        const input1 = new InputSlot(1, Boolean.className, Boolean.defaultValue, true, "Condition");
        const input2 = new InputSlot(2, Number.className, 10000, true, "Limit");
        const inputSlots = [input1, input2];

        const outputSlots: OutputSlot[] = [];

        const inputFlowSlots = [new InputFlowSlot(0, (thisSlot, returnToSlot) => {
            if (!thisSlot.returnToSlot)
                thisSlot.returnToSlot = returnToSlot;
            if (this.limit == null) {
                this.limit = input2.getValue(this.itemId);
            }
            
            if (this.limit == null)
                console.error(`The limit of while loop of id '${this.itemId}' is undefined, therefore this while loop won't run. Set it to 0 if it is intended to run forever.`);

            if (input1.getValue(this.itemId) && this.limit != null && this.counter < (this.limit <= 0 ? 1 : this.limit)) {
                if (this.limit > 0)
                    this.counter += 1;

                outputFlow1.goNext(this.itemId, {id: -1, itemId: this.itemId, slotId: thisSlot.id});
            } else {
                const returnTo = thisSlot.returnToSlot;
                thisSlot.returnToSlot = undefined;
                this.counter = 0;
                this.limit = undefined;
                outputFlow2.goNext(this.itemId, returnTo);
            }
        }, true, "Entry")];

        super(id, name, uniqueNamePrefix, 200, inputFlowSlots, outputFlowSlots, inputSlots, outputSlots);
        this.counter = 0;
    }

    createNewSchema(): WhileLoopSchema {
        return new WhileLoopSchema();
    }
}

