// The base class for all addition 
// We have two choices:
// - Extend from this AddSchema to make addition nodes for different object types
//   - Can customize every aspect of the node
// - Create a universal way to add same-typed objects together through interfaces
//   - Will only need one universal addition node that uses the interface to add objects
@AddSchema
export class AddNumbersSchema extends BaseNodeSchema {
    constructor() {
        const id = BaseSchemaTypes.AddNumbers;
        const name = "Add Numbers";
        const uniqueNamePrefix = "addNums";
        const input1 = new InputSlot(0, Number.className, Number.defaultValue);
        const input2 = new InputSlot(1, Number.className, Number.defaultValue);
        const inputSlots = [input1, input2];
        const outputSlots = [new OutputSlot(0, Number.className, () => {
            // TODO: Type check maybe?
            return input1.getValue(this.itemId) + input2.getValue(this.itemId);
        }, "Sum")];

        super(id, name, uniqueNamePrefix, 200, [], [], inputSlots, outputSlots);
    }

    createNewSchema(): AddNumbersSchema {
        return new AddNumbersSchema();
    }
}

