@AddSchema
export class ModuloNumbersSchema extends BaseNodeSchema {
    constructor() {
        const id = BaseSchemaTypes.ModuloNumbers;
        const name = "Modulo Numbers";
        const uniqueNamePrefix = "moduloNums";
        const input1 = new InputSlot(0, Number.className, Number.defaultValue);
        const input2 = new InputSlot(1, Number.className, Number.defaultValue);
        const inputSlots = [input1, input2];
        const outputSlots = [new OutputSlot(0, Number.className, () => {
            const num1 = input1.getValue(this.itemId);
            const num2 = input2.getValue(this.itemId);
            return ((num1 % num2) + num2) % num2;
        }, "Modulo")];

        super(id, name, uniqueNamePrefix, 205, [], [], inputSlots, outputSlots);
    }

    createNewSchema(): ModuloNumbersSchema {
        return new ModuloNumbersSchema();
    }
}

