import { BaseNodeSchema } from "../models/NodeSchema";

class NodeSchemaManager {
    public nodeSchemas: Map<string, BaseNodeSchema>;

    constructor() {
        this.nodeSchemas = new Map<string, BaseNodeSchema>();
    }

    getNewSchema(itemId: number, schemaId: string): BaseNodeSchema {
        const schema = this.nodeSchemas.get(schemaId);
        if (!schema)
            throw new Error(`The schema '${schemaId}' could not be found.`);
        return schema.createNew(itemId);
    }

    getBaseSchema(id: string): BaseNodeSchema {
        const schema = this.nodeSchemas.get(id);
        if (!schema)
            throw new Error(`The schema '${id}' could not be found.`);
        return schema;
    }

    getBaseSchemas(): BaseNodeSchema[] {
        return [...this.nodeSchemas.values()];
    }
}

export default new NodeSchemaManager();