import { KonvaEventObject } from "konva/types/Node";

export class KonvaUtils {
    public static setCursor(event: KonvaEventObject<Event>, cursorType: string) {
        const stage = event.target.getStage();
        if (!stage) {
            console.error(`The Konva stage could not be found.`);
            return;
        }

        stage.container().style.cursor = cursorType;
    }
}