import React from 'react';
import { Circle, Path } from 'react-konva';
import { HandleType } from '../managers/SlotConfigManager';
import { KonvaForm } from './KonvaForm';
import { KonvaEventObject } from 'konva/types/Node';
import Konva from 'konva';
import Colors from '../utils/Colors';
import { Vector2d } from 'konva/types/types';
import { KonvaUtils } from '../utils/KonvaUtils';

export type NodeHandleProps = {
    fill: string;
    stroke: string;
    type: HandleType;
    isInput: boolean;
    isFlow: boolean;
    itemId: number;
    slotId: number;
    onConnect: (otherItemId: number, otherSlotId: number) => void;
}

export type NodeHandleState = {
    startPosition: Vector2d;
}

export class NodeHandle extends React.Component<NodeHandleProps, NodeHandleState> {
    private static path: Konva.Path | null = null;

    constructor(props: NodeHandleProps) {
        super(props);

        this.state = {
            startPosition: {x: 0, y: 0},
        }
        
        this.onDragStart = this.onDragStart.bind(this);
        this.onDragMove = this.onDragMove.bind(this);
        this.onDragEnd = this.onDragEnd.bind(this);
        this.onMouseEnter = this.onMouseEnter.bind(this);
        this.onMouseExit = this.onMouseExit.bind(this);
    }

    onDragStart(event: KonvaEventObject<DragEvent>) {
        const layer: Konva.Layer | null = event.target.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        KonvaForm.cancel();
        
        const thisCircle = event.target;
        const parent = thisCircle.parent;
        if (!parent) {
            console.error(`This Konva node should be under a container node.`);
            return;
        }

        const startPos = thisCircle.getPosition();
        const endPos = startPos;
        const pathData = `M${startPos.x} ${startPos.y - 2} L${endPos.x} ${endPos.y - 2}`;
        const path = new Konva.Path({
            data: pathData,
            stroke: Colors.Gray[800],
            strokeWidth: 4
        });

        parent.add(path);
        NodeHandle.path = path;
        this.setState({startPosition: startPos});
    }

    onDragMove(event: KonvaEventObject<DragEvent>) {
        const { isInput } = this.props;
        const { startPosition } = this.state;

        const layer: Konva.Layer | null = event.target.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }
        
        const thisCircle = event.target;
        const position = thisCircle.getPosition();
        let startPos, endPos;

        if (isInput) {
            // The isInput specifies if its either the input handle or the output
            // handle that's being dragged.
            // Gotta flip the start and end positions so the path forms an S shape
            // instead of a mirrored S shape when dragged.
            startPos = position;
            endPos = startPosition;
        } else {
            startPos = startPosition;
            endPos = position;
        }

        const midpointPos: Vector2d = {
            x: ((endPos.x - startPos.x) / 2) + (startPos.x),
            y: ((endPos.y - startPos.y) / 2) + (startPos.y)
        }
        const pathData = `M${startPos.x} ${startPos.y - 2} Q${startPos.x + 30} ${startPos.y - 2} ${midpointPos.x} ${midpointPos.y - 2} Q${endPos.x - 30} ${endPos.y - 2} ${endPos.x} ${endPos.y - 2}`;
        const path = NodeHandle.path;
        if (!path) {
            console.error(`The Konva path could not be found.`);
            return;
        }

        path.data(pathData);
    }

    onDragEnd(event: KonvaEventObject<DragEvent>) {
        const { isInput, isFlow, itemId, onConnect } = this.props;
        const { startPosition } = this.state;

        const thisCircle = event.target;
        thisCircle.position(startPosition);

        const path = NodeHandle.path;
        if (path != null) {
            path.destroy();
            NodeHandle.path = null;
        }

        const stage = thisCircle.getStage();
        if (!stage) {
            console.error(`The Konva stage could not be found.`);
            return;
        }

        stage.draw();

        const pointerPos = stage.getPointerPosition();
        if (!pointerPos) {
            console.error(`The Konva pointer position could not be found.`);
            return;
        }

        const screenX = Math.floor(pointerPos.x);
        const screenY = Math.floor(pointerPos.y);

        const elements = stage.getAllIntersections({x: screenX, y: screenY}) as Konva.Shape[];
        
        if (!elements || elements.length <= 0)
            return;

        let element: Konva.Shape | undefined;
        let otherItemId: number = 0;
        let otherSlotId: number = 0;

        let i;
        for (i = 0; i < elements.length; i++) {
            const elem = elements[i];
            const type = elem.getAttr("data-type");
            if (!type || (isInput ? type !== "output" : type !== "input"))
                continue;
            const slotType = elem.getAttr("data-slot-type");
            if (!slotType || (isFlow ? slotType !== "control" : slotType !== "data"))
                continue;

            otherItemId = elem.getAttr("data-item-id");
            otherSlotId = elem.getAttr("data-slot-id");
            // NOTE: These ids can be 0 and !id will return true
            // TODO: Make ids to be unique uuids
            if (otherItemId == null || otherSlotId == null) {
                console.error(`No attributes 'data-item-id' or 'data-slot-id' could be found from the element.`);
                continue;
            }

            if (itemId === otherItemId)
                continue;

            element = elem;
            break;
        }

        if (!element)
            return;

        onConnect(otherItemId, otherSlotId);
    }

    onMouseEnter(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "pointer");
    }

    onMouseExit(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "default");
    }

    render() {
        const { fill, stroke, type, isInput, isFlow, itemId, slotId } = this.props;
        
        return(
            <>
            {(type === HandleType.Circle &&
                <Circle x={isInput ? 18 : -18}
                    y={18}
                    radius={4}
                    fill={fill}
                    stroke={stroke}
                    strokeWidth={1}
                    scale={{x: 1.5, y: 1.5}}
                    listening={false} />
            ) ||
            (type === HandleType.Arrow &&
                <Path data="M6.659,13.549L12.139,8.753C12.356,8.563 12.481,8.288 12.481,8C12.481,7.712 12.356,7.437 12.139,7.247L6.66,2.451C6.011,1.885 5,2.345 5,3.204L5,12.796C5,12.796 5,12.797 5,12.797C5,13.345 5.451,13.797 6,13.797C6.242,13.797 6.477,13.709 6.659,13.549Z"
                    x={isInput ? 5 : -31}
                    y={6}
                    fill={fill}
                    stroke={stroke}
                    strokeWidth={1}
                    scale={{x: 1.5, y: 1.5}}
                    listening={false} />
            )}

            <Circle data-type={`${isInput ? "input" : "output"}`}
                    data-slot-type={`${isFlow ? "control" : "data"}`}
                    data-item-id={itemId}
                    data-slot-id={slotId}
                    x={isInput ? 18 : -18}
                    y={22}
                    radius={15}
                    fill="transparent"
                    draggable
                    onDragStart={event => {
                        event.cancelBubble = true;
                        this.onDragStart(event);
                    }}
                    onDragMove={event => {
                        event.cancelBubble = true;
                        this.onDragMove(event);
                    }}
                    onDragEnd={event => {
                        event.cancelBubble = true;
                        this.onDragEnd(event);
                    }}
                    onMouseEnter={this.onMouseEnter}
                    onMouseLeave={this.onMouseExit} />
            </>
        );
    }
}