import React from 'react';
import logo from './logo.svg';
import './App.css';
import './assets/main.css';

import { library } from '@fortawesome/fontawesome-svg-core';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { Switch, Route, useParams } from 'react-router-dom';

import SidebarNav from './components/SidebarNav';
import ScripletRoot from './components/ScripletRoot';
import CodeEditor from './components/CodeEditor';

library.add(fas);

function App() {
    return(
        <div className="flex flex-col lg:flex-row">
            <SidebarNav />
            <main>
                <Switch>
                    <Route path="/scriplet/:scripletId">
                        <ParamsProvider>
                            {(scripletId) => (
                                <>
                                    <ScripletRoot scripletId={scripletId} />
                                    <CodeEditor scripletId={scripletId} />
                                </>
                            )}
                        </ParamsProvider>
                    </Route>
                    <Route path="/">
                        <div className="p-6 flex justify-center items-center h-screen w-screen">
                            <h1 className="text-xl text-gray-600">
                                Please select a scriplet first.
                            </h1>
                        </div>
                    </Route>
                </Switch>
            </main>
        </div>
    );
}

type ParamsProps = {
    children?(scripletId: number): React.ReactNode | null;
}

function ParamsProvider({children}: ParamsProps): JSX.Element {
    const { scripletId } = useParams();
    return (
        <>
            {children != null ? children(parseInt(scripletId)) : null}
        </>
    );
}

export default App;
