import NodeConnections from "../managers/NodeConnections";
import ScripletManager from "../managers/ScripletManager";

export type ItemSlotPair = {
    id: number;
    itemId: number;
    slotId: number;
}

export abstract class NodeSlot {
    public id: number;
    public name?: string;

    constructor(id: number, name?: string) {
        this.id = id;
        this.name = name;
    }
}

export abstract class TypedNodeSlot extends NodeSlot {
    public type: string;

    constructor(id: number, type: string, name?: string) {
        super(id, name);
        this.type = type;
    }

    isCorrectType(type: string): boolean {
        if (this.type === type)
            return true;
        return false;
    }
}

export type IdValuePair = {
    id: number;
    value?: any;
}

export class InputSlot extends TypedNodeSlot {
    //public connectedToOutput: ItemSlotPair | undefined;
    // TODO: This value should also be serialized to db.
    public defaultValue: any;
    public isConnectable: boolean;

    constructor(id: number, type: string, defaultValue: any, isConnectable?: boolean, name?: string) {
        super(id, type, name);
        this.defaultValue = defaultValue;
        this.isConnectable = isConnectable ?? true;
    }

    getValue(itemId: number) {
        const scriplet = ScripletManager.currentScriplet;
        if (!scriplet) {
            console.error(`The current scriplet could not be found.`);
            return;
        }

        const item = scriplet.getNode(itemId);
        if (!item) {
            console.error(`The node item of id '${itemId}' could not be found.`);
            return;
        }

        const connectedTo = item.getInputConnection(this.id);
        if (!connectedTo)
            return item.getInputValue(this.id) ?? this.defaultValue;

        const otherSlot = NodeConnections.getOutputSlot(connectedTo.itemId, connectedTo.slotId);
        if (!otherSlot) {
            console.error(`The output slot '${connectedTo.slotId}' of node item '${connectedTo.itemId}' could not be found.`);
            return item.getInputValue(this.id) ?? this.defaultValue;
        }

        return otherSlot.evaluate();
    }
}

// TODO: Flow output slots cannot be connected to multiple Flow input slots
export class OutputSlot extends TypedNodeSlot {
    //public connectedToInputs: ItemSlotPair[] = [];
    public evaluate: () => any;

    constructor(id: number, type: string, evaluate: () => any, name?: string) {
        super(id, type, name);
        this.evaluate = evaluate;
    }
}

export abstract class FlowNodeSlot extends NodeSlot { }

export class InputFlowSlot extends FlowNodeSlot {
    //public connectedToOutput: ItemSlotPair | undefined;
    public isConnectable: boolean;
    private evaluate: (thisSlot: InputFlowSlot, returnToSlot?: ItemSlotPair) => any;

    public returnToSlot: ItemSlotPair | undefined;

    constructor(id: number, evaluate: (thisSlot: InputFlowSlot, returnToSlot?: ItemSlotPair) => any, isConnectable?: boolean, name?: string) {
        super(id, name);
        this.evaluate = evaluate;
        this.isConnectable = isConnectable ?? true;
    }

    evaluateSlot(returnToSlot?: ItemSlotPair) {
        this.evaluate(this, returnToSlot);
    }
}

export class OutputFlowSlot extends FlowNodeSlot {
    //public connectedToInput: ItemSlotPair | undefined;

    goNext(itemId: number, returnToSlot?: ItemSlotPair) {
        const scriplet = ScripletManager.currentScriplet;
        if (!scriplet) {
            console.error(`The current scriplet could not be found.`);
            return;
        }

        const item = scriplet.getNode(itemId);
        if (!item) {
            console.error(`The node item of id '${itemId}' could not be found.`);
            return;
        }

        const connectedTo = item.getOutputFlowConnection(this.id);
        if (connectedTo) {
            const inputSlot = NodeConnections.getInputFlowSlot(connectedTo.itemId, connectedTo.slotId);
            if (!inputSlot) {
                console.error(`The input flow slot '${connectedTo.slotId}' of node item '${connectedTo.itemId}' could not be found.`)
                return;
            }

            inputSlot.evaluateSlot(returnToSlot);
            return;
        }

        if (returnToSlot) {
            // Return to where the returnToSlot points to
            const returnSlot = NodeConnections.getInputFlowSlot(returnToSlot.itemId, returnToSlot.slotId);
            if (!returnSlot) {
                console.error(`The input flow slot '${returnToSlot.slotId}' of node item '${returnToSlot.itemId}' could not be found.`)
                return;
            }

            returnSlot.evaluateSlot();
            return;
        }
    }
}