import { Scriplet } from "../models/Scriplet";
import VisualScriptingDB from "./VisualScriptingDB";

class ScripletManager {
    public currentScriplet: Scriplet | undefined;

    // Current Scriplet can be undefined if none was selected yet
    async selectScriplet(id: number): Promise<boolean> {
        return await VisualScriptingDB.getScriplet(id).then(scriplet => {
            console.log(scriplet);
            this.currentScriplet = scriplet;
            return true;
        }).catch(error => {
            console.error(`Something went wrong when getting the scriplet of id '${id}'. Error: ${error}`);
            return false;
        });
    }
}

export default new ScripletManager();