import React from 'react';
import { Group, Text } from 'react-konva';
import Konva from 'konva';
import Colors from '../utils/Colors';
import { Input, Dropdown } from './KonvaForm';
import SlotConfigManager, { InputType, HandleType } from '../managers/SlotConfigManager';
import { NodeHandle } from './NodeHandle';
import { InputSlot, OutputSlot, InputFlowSlot, OutputFlowSlot, ItemSlotPair } from '../models/NodeSlot';
import ScripletManager from '../managers/ScripletManager';

export type InputSlotProps = {
    inputSlot: InputSlot;
    inputConnection: ItemSlotPair | undefined;
    x: number;
    y: number;
    itemId: number;
    onInputChange: (value: any) => boolean;
    onConnect: (otherItemId: number, otherSlotId: number) => void;
}

export type InputSlotState = {
    labelWidth: number;
    typeWidth: number;
}

export class InputSlotDOM extends React.Component<InputSlotProps, InputSlotState> {
    private label: React.RefObject<Konva.Text>;
    private type: React.RefObject<Konva.Text>;

    constructor(props: InputSlotProps) {
        super(props);

        this.label = React.createRef();
        this.type = React.createRef();

        this.state = {
            labelWidth: 100,
            typeWidth: 100
        };

        this.onInputChanged = this.onInputChanged.bind(this);
    }

    componentDidMount() {
        const labelWidth = this.label.current?.width();
        const typeWidth = this.type.current?.width();
        if (!labelWidth || !typeWidth) {
            console.error(`The referenced Konva label or type text could not be found.`);
            return;
        }

        this.setState({labelWidth: labelWidth, typeWidth: typeWidth});
    }

    onInputChanged(value: string): boolean {
        const { inputSlot, onInputChange } = this.props;
        const config = SlotConfigManager.getConfig(inputSlot.type);
        if (!config) {
            console.error(`Cannot find the input initial values for the given type '${inputSlot.type}'.`);
            return false;
        }

        const val = config.fromString(value);
        if (val == null)
            return false;

        return onInputChange(val);
    }

    render() {
        const { inputSlot, inputConnection, x, y, itemId, onConnect } = this.props;
        const { labelWidth, typeWidth } = this.state;
        const config = SlotConfigManager.getConfig(inputSlot.type);
        if (!config) {
            console.error(`Cannot find the input initial values for the given type '${inputSlot.type}'.`);
            return null;
        }

        const item = ScripletManager.currentScriplet?.getNode(itemId);
        if (!item) {
            console.error(`The node item of id '${itemId}' could not be found.`);
            return null;
        }

        return(
            <Group x={x}
                    y={y}>
                <Text ref={this.label}
                        x={inputSlot.isConnectable ? 36 : 16}
                        y={0}
                        height={20}
                        fontSize={12}
                        fontStyle="bold"
                        verticalAlign="bottom"
                        fill={Colors.Gray[100]}
                        text={inputSlot.name} />
                <Text ref={this.type}
                        x={inputSlot.isConnectable ? 36 : 16}
                        y={20}
                        height={20}
                        fontSize={10}
                        verticalAlign="top"
                        fill={Colors.Gray[100]}
                        text={inputSlot.type} />
                {(!inputConnection &&
                ((config.inputType === InputType.Input &&
                    <Input x={inputSlot.isConnectable ? 44 + Math.max(labelWidth, typeWidth) : 24 + Math.max(labelWidth, typeWidth)}
                            y={12}
                            width={30}
                            height={12}
                            fontSize={10}
                            padding={2}
                            borderRadius={2}
                            textColor={Colors.Gray[100]}
                            bgColor={Colors.Gray[700]}
                            hoverColor={Colors.Gray[600]}
                            value={`${item.getInputValue(inputSlot.id) ?? inputSlot.defaultValue}`}
                            onChange={this.onInputChanged} />
                ) ||
                (config.inputType === InputType.Dropdown &&
                    <Dropdown x={inputSlot.isConnectable ? 44 + Math.max(labelWidth, typeWidth) : 24 + Math.max(labelWidth, typeWidth)}
                            y={12}
                            width={30}
                            height={12}
                            fontSize={10}
                            padding={2}
                            borderRadius={2}
                            textColor={Colors.Gray[100]}
                            bgColor={Colors.Gray[700]}
                            hoverColor={Colors.Gray[600]}
                            values={config.defaultInputValue as string[]}
                            onChange={this.onInputChanged} />
                )))}
                {(inputSlot.isConnectable &&
                    <NodeHandle fill={inputConnection ? config.handleFillColor : "transparent"}
                                stroke={config.handleBorderColor}
                                type={config.handleType}
                                isInput={true}
                                isFlow={false}
                                itemId={itemId}
                                slotId={inputSlot.id}
                                onConnect={onConnect} />
                )}
            </Group>
        );
    }
}

export type OutputSlotProps = {
    outputSlot: OutputSlot;
    outputConnections: ItemSlotPair[];
    x: number;
    y: number;
    itemId: number;
    onConnect: (otherItemId: number, otherSlotId: number) => void;
};

export type OutputSlotState = {
    labelWidth: number;
    typeWidth: number;
}

export class OutputSlotDOM extends React.Component<OutputSlotProps, OutputSlotState> {
    private label: React.RefObject<Konva.Text>;
    private type: React.RefObject<Konva.Text>;

    constructor(props: OutputSlotProps) {
        super(props);

        this.label = React.createRef();
        this.type = React.createRef();

        this.state = {
            labelWidth: 100,
            typeWidth: 100
        };
    }

    componentDidMount() {
        const labelWidth = this.label.current?.width();
        const typeWidth = this.type.current?.width();
        if (!labelWidth || !typeWidth) {
            console.error(`The referenced Konva label or type text could not be found.`);
            return;
        }

        this.setState({labelWidth: labelWidth, typeWidth: typeWidth});
    }

    render() {
        const { outputSlot, outputConnections, x, y, itemId, onConnect } = this.props;
        const { labelWidth, typeWidth } = this.state;
        const config = SlotConfigManager.getConfig(outputSlot.type);
        if (!config) {
            console.error(`Cannot find the input initial values for the given type '${outputSlot.type}'.`);
            return null;
        }

        return(
            <Group x={x}
                    y={y}>
                <Text ref={this.label}
                        x={0}
                        y={0}
                        offsetX={36 + labelWidth}
                        height={20}
                        fontSize={12}
                        fontStyle="bold"
                        align="right"
                        verticalAlign="bottom"
                        fill={Colors.Gray[100]}
                        text={outputSlot.name} />
                <Text ref={this.type}
                        x={0}
                        y={20}
                        offsetX={36 + typeWidth}
                        height={20}
                        fontSize={10}
                        align="right"
                        verticalAlign="top"
                        fill={Colors.Gray[100]}
                        text={outputSlot.type} />
                <NodeHandle fill={outputConnections.length > 0 ? config.handleFillColor : "transparent"}
                            stroke={config.handleBorderColor}
                            type={config.handleType}
                            isInput={false}
                            isFlow={false}
                            itemId={itemId}
                            slotId={outputSlot.id}
                            onConnect={onConnect} />
            </Group>
        );
    }
}

export type InputFlowSlotProps = {
    inputFlowSlot: InputFlowSlot;
    inputFlowConnection: ItemSlotPair | undefined;
    x: number;
    y: number;
    itemId: number;
    onConnect: (otherItemId: number, otherSlotId: number) => void;
}

export type InputFlowSlotState = {
    
}

export class InputFlowSlotDOM extends React.Component<InputFlowSlotProps, InputFlowSlotState> {
    render() {
        const { inputFlowSlot, inputFlowConnection, x, y, itemId, onConnect } = this.props;

        return(
            <Group x={x}
                    y={y}>
                <Text x={inputFlowSlot.isConnectable ? 36 : 16}
                        y={0}
                        height={40}
                        fontSize={12}
                        fontStyle="bold"
                        verticalAlign="middle"
                        fill={Colors.Gray[100]}
                        text={inputFlowSlot.name} />
                {(inputFlowSlot.isConnectable && 
                    <NodeHandle fill={inputFlowConnection ? Colors.Gray[100] : "transparent"}
                                stroke={Colors.Gray[500]}
                                type={HandleType.Arrow}
                                isInput={true}
                                isFlow={true}
                                itemId={itemId}
                                slotId={inputFlowSlot.id}
                                onConnect={onConnect} />
                )}
            </Group>
        );
    }
}

export type OutputFlowSlotProps = {
    outputFlowSlot: OutputFlowSlot;
    outputFlowConnection: ItemSlotPair | undefined;
    x: number;
    y: number;
    itemId: number;
    onConnect: (otherItemId: number, otherSlotId: number) => void;
}

export type OutputFlowSlotState = {
    labelWidth: number;
}

export class OutputFlowSlotDOM extends React.Component<OutputFlowSlotProps, OutputFlowSlotState> {
    private label: React.RefObject<Konva.Text>;

    constructor(props: OutputFlowSlotProps) {
        super(props);

        this.label = React.createRef();

        this.state = {
            labelWidth: 100
        };
    }

    componentDidMount() {
        const labelWidth = this.label.current?.width();
        if (!labelWidth) {
            console.error(`The referenced Konva label text could not be found.`);
            return;
        }

        this.setState({labelWidth: labelWidth});
    }
    
    render() {
        const { outputFlowSlot, outputFlowConnection, x, y, itemId, onConnect } = this.props;
        const { labelWidth } = this.state;

        return(
            <Group x={x}
                    y={y}>
                <Text ref={this.label}
                        x={0}
                        y={0}
                        offsetX={36 + labelWidth}
                        height={40}
                        fontSize={12}
                        fontStyle="bold"
                        align="right"
                        verticalAlign="middle"
                        fill={Colors.Gray[100]}
                        text={outputFlowSlot.name} />
                <NodeHandle fill={outputFlowConnection ? Colors.Gray[100] : "transparent"}
                            stroke={Colors.Gray[500]}
                            type={HandleType.Arrow}
                            isInput={false}
                            isFlow={true}
                            itemId={itemId}
                            slotId={outputFlowSlot.id}
                            onConnect={onConnect} />
            </Group>
        );
    }
}