declare global {
    interface Array<T> {
        removeIf(predicate: (input: T) => boolean): any;
        indexOfWhen(predicate: (input: T) => boolean): any;
    }
}

Array.prototype.indexOfWhen = function(predicate) {
    let i = 0;
    while (i < this.length) {
        if (predicate(this[i]))
            return i;
        i++;
    }
}

export {};