import Colors from "../utils/Colors";
import { Number, Boolean, Flow } from "../utils/Types";

export enum HandleType {
    Circle,
    Arrow
}

export enum InputType {
    None,
    Input,
    Dropdown
}

export type SlotConfig = {
    // handle config
    handleType: HandleType;
    handleBorderColor: string;
    handleFillColor: string;

    inputType: InputType;
    defaultInputValue: string | string[];
    initialValue: any;

    fromString: (value: string) => any;
    toString: (value: any) => string;
}

class SlotConfigManager {
    private configs: Map<string, SlotConfig>;

    constructor() {
        this.configs = new Map<string, SlotConfig>();

        const numberConfig: SlotConfig = {
            handleType: HandleType.Circle,
            handleBorderColor: Colors.Green[700],
            handleFillColor: Colors.Green[600],
            inputType: InputType.Input,
            defaultInputValue: "",
            initialValue: Number.defaultValue,
            fromString: Number.convertFromString,
            toString: Number.convertToString
        };

        const booleanConfig: SlotConfig = {
            handleType: HandleType.Circle,
            handleBorderColor: Colors.Purple[600],
            handleFillColor: Colors.Purple[500],
            inputType: InputType.Dropdown,
            defaultInputValue: ["true", "false"],
            initialValue: Boolean.defaultValue,
            fromString: Boolean.convertFromString,
            toString: Boolean.convertToString
        };

        const flowConfig: SlotConfig = {
            handleType: HandleType.Arrow,
            handleBorderColor: Colors.Gray[500],
            handleFillColor: Colors.Gray[100],
            inputType: InputType.None,
            defaultInputValue: "",
            initialValue: Flow.defaultValue,
            fromString: Flow.convertFromString,
            toString: Flow.convertToString
        }

        this.configs.set(Number.className, numberConfig);
        this.configs.set(Boolean.className, booleanConfig);
        this.configs.set(Flow.className, flowConfig);
    }

    getConfig(type: string) {
        return this.configs.get(type);
    }
}

export default new SlotConfigManager();

