import NodeSchemaManager from "./NodeSchemaManager";

class VariableManager {
    private typeCount: Map<string, number>;

    constructor() {
        this.typeCount = new Map<string, number>();
    }

    getNextVariableName(schemaId: string): string | undefined {
        const schema = NodeSchemaManager.getBaseSchema(schemaId);
        if (!schema) {
            console.error(`The schema of id '${schemaId}' could not be found.`);
            return;
        }
        
        let count = this.typeCount.get(schemaId);
        // This shouldn't be written as !count because having a value of 0 will always return true.
        if (count == null) {
            this.typeCount.set(schemaId, 1);
            count = 1;
        }
        else
            this.typeCount.set(schemaId, count + 1);

        return `${schema.uniqueNamePrefix}${count}`;
    }
}

export default new VariableManager();