import { BaseNodeSchema } from "../models/NodeSchema";
import NodeSchemaManager from "./NodeSchemaManager";

export class Category {
    public name: string;
    public subCategories: Category[];
    public contentChildren: BaseNodeSchema[];

    constructor(name: string) {
        this.name = name;
        this.subCategories = [];
        this.contentChildren = [];
    }

    getSubCategory(name: string) {
        let subCategory = this.subCategories.find(cat => cat.name === name);
        if (!subCategory) {
            subCategory = new Category(name);
            this.subCategories.push(subCategory);
        }

        return subCategory;
    }

    addContent(id: string) {
        if (this.contentChildren.find(schema => schema.name === id)) {
            console.error(`A duplicate of the schema '${id}' was found.`);
            return;
        }

        const schema = NodeSchemaManager.getBaseSchema(id);
        this.contentChildren.push(schema);
    }
}

class CategoryManager {
    getRootSchemaCategory(schemas: BaseNodeSchema[]): Category {
        const categoryRoot = new Category("root");

        let currentCategory = categoryRoot;
        schemas.forEach(schema => {
            const idArr = schema.id.split("/");
            let i;
            for (i = 0; i < idArr.length - 1; i++) {
                currentCategory = currentCategory.getSubCategory(idArr[i]);
            }
            currentCategory.addContent(schema.id);
            currentCategory = categoryRoot;
        });

        return categoryRoot;
    }
}

export default new CategoryManager();