import React from 'react';
import { Label, Tag, Text } from 'react-konva';
import { KonvaEventObject } from 'konva/types/Node';
import { KonvaUtils } from '../utils/KonvaUtils';

export class KonvaForm {
    public static cancel() {
        Input.cancelInput();
        Dropdown.cancelDropdown();
    }
} 

export type InputModel = {
    value: string;
    type: string;
    onChange: (value: string) => boolean;
}

export type OutputModel = {
    values: string[];
    onChange: (value: string) => boolean;
}

export type InputProps = {
    x: number;
    y: number;
    width: number;
    height: number;
    fontSize: number;
    fontStyle: string;
    padding: number;
    textColor: string;
    bgColor: string;
    hoverColor: string;
    borderRadius: number;
    zIndex: number;
    value: string;
    type: string;
    onChange: (value: string) => boolean;
}

export type InputState = {
    value: string;
}

export class Input extends React.Component<InputProps, InputState> {
    private static inputElement: HTMLInputElement;
    
    static defaultProps: InputProps = {
        x: 0,
        y: 0,
        width: 100,
        height: 20,
        fontSize: 12,
        fontStyle: "normal",
        padding: 0,
        textColor: "#000000",
        bgColor: "#ffffff",
        hoverColor: "#cccccc",
        borderRadius: 0,
        zIndex: 0,
        value: "",
        type: "text",
        onChange: () => { return false; }
    }

    constructor(props: InputProps) {
        super(props);

        this.state = {
            value: ""
        }

        this.showInput = this.showInput.bind(this);
        this.onHover = this.onHover.bind(this);
        this.onHoverExit = this.onHoverExit.bind(this);
    }

    showInput(event: KonvaEventObject<Event>) {
        Input.cancelInput();

        const { width, height, fontSize, padding, textColor, hoverColor, borderRadius, zIndex, type, onChange } = this.props;
        const value = this.state.value === "" ? this.props.value : this.state.value;

        const stage = event.target.getStage();
        if (!stage) {
            console.error(`The Konva stage could not be found.`);
            return;
        }
        const scale = stage.scaleX();

        const top = event.target.absolutePosition().y;
        const left = event.target.absolutePosition().x;

        const input = document.createElement("input");
        document.body.appendChild(input);

        input.value = value;
        input.type = type;
        input.style.position = "absolute";
        input.style.top = `${top}px`;
        input.style.left = `${left}px`;
        input.style.width = `${Math.min(width * scale, window.innerWidth - left)}px`;
        input.style.height = `${height * scale}px`;
        input.style.paddingLeft = `${padding * scale}px`;
        input.style.fontSize = `${fontSize * scale}px`;
        input.style.color = textColor;
        input.style.backgroundColor = hoverColor;
        input.style.borderRadius = `${borderRadius * scale}px`;
        input.style.zIndex = `${zIndex}`;
        input.focus();
        input.onchange = (ev: Event) => {
            const target = ev.target as HTMLInputElement;

            if (!target) {
                console.error(`Cannot cast target to 'HTMLInputElement'.`);
                Input.cancelInput();
                return;
            }

            if (!target.value) {
                console.error(`No value typed in while editing the text.`);
                Input.cancelInput();
                return;
            }

            if (!onChange(target.value)) {
                console.error(`Input value requirements not met.`);
                Input.cancelInput();
                return;
            }

            onChange(target.value);
            this.setState({value: target.value});
            Input.cancelInput();
        }

        Input.inputElement = input;
    }

    public static cancelInput() {
        const input = Input.inputElement;
        if (!input)
            return;
        
        input.onchange = null;
        input.parentNode?.removeChild(input);
    }

    onHover(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "text");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const textBG = event.currentTarget.children[0];
        textBG.setAttr("fill", this.props.hoverColor);
        layer.draw();
    }

    onHoverExit(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "default");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const textBG = event.currentTarget.children[0];
        textBG.setAttr("fill", this.props.bgColor);
        layer.draw();
    }

    render() {
        const { x, y, width, height, fontSize, fontStyle, padding, textColor, bgColor, borderRadius } = this.props;
        const value = this.state.value === "" ? this.props.value : this.state.value;

        return(
            <Label name="top-bar-label"
                x={x}
                y={y}
                onMouseEnter={this.onHover}
                onMouseLeave={this.onHoverExit} 
                onClick={event => {
                    event.cancelBubble = true;
                    this.showInput(event);
                }}
                onTap={event => {
                    event.cancelBubble = true;
                    this.showInput(event);
                }}>
                <Tag fill={bgColor}
                        cornerRadius={borderRadius} />
                <Text name="top-bar-text"
                        width={width}
                        height={height}
                        fontStyle={fontStyle}
                        fontSize={fontSize}
                        verticalAlign="middle"
                        padding={padding}
                        fill={textColor}
                        text={value} />
            </Label>
        );
    }
}

export type DropdownProps = {
    x: number;
    y: number;
    width: number;
    height: number;
    fontSize: number;
    fontStyle: string;
    padding: number;
    textColor: string;
    bgColor: string;
    hoverColor: string;
    borderRadius: number;
    zIndex: number;
    values: string[];
    onChange: (value: string) => boolean;
}

export type DropdownState = {
    selectedIndex: number;
}

export class Dropdown extends React.Component<DropdownProps, DropdownState> {
    private static dropdownElement: HTMLSelectElement;
    
    static defaultProps: DropdownProps = {
        x: 0,
        y: 0,
        width: 100,
        height: 20,
        fontSize: 12,
        fontStyle: "normal",
        padding: 0,
        textColor: "#000000",
        bgColor: "#ffffff",
        hoverColor: "#cccccc",
        borderRadius: 0,
        zIndex: 0,
        values: [],
        onChange: () => { return false; }
    }

    constructor(props: DropdownProps) {
        super(props);

        this.state = {
            selectedIndex: 0
        };

        this.showDropdown = this.showDropdown.bind(this);
        this.onHover = this.onHover.bind(this);
        this.onHoverExit = this.onHoverExit.bind(this);
    }

    showDropdown(event: KonvaEventObject<Event>) {
        Dropdown.cancelDropdown();

        const { width, height, padding, fontSize, textColor, hoverColor, borderRadius, zIndex, values, onChange } = this.props;

        const stage = event.target.getStage();
        if (!stage) {
            console.error(`The Konva stage could not be found.`);
            return;
        }
        const scale = stage.scaleX();

        const top = event.target.absolutePosition().y;
        const left = event.target.absolutePosition().x;

        const dropdown = document.createElement("select");
        document.body.appendChild(dropdown);

        // Add classes here
        //dropdown.classList.add("block", "appearance-none", "w-full", "bg-white", "border", "border-gray-400", "hover:border-gray-500", "px-4", "py-2", "pr-8", "rounded", "shadow", "leading-tight", "focus:outline-none", "focus:shadow-outline");
        values.forEach(value => {
            const option = document.createElement("option");
            option.value = value;
            option.innerText = value;
            dropdown.add(option);
        });

        dropdown.selectedIndex = this.state.selectedIndex;
        dropdown.style.position = "absolute";
        dropdown.style.top = `${top}px`;
        dropdown.style.left = `${left}px`;
        dropdown.style.width = `${Math.min(width * scale, window.innerWidth - left)}px`;
        dropdown.style.height = `${height * scale}px`;
        dropdown.style.paddingLeft = `${padding * scale}px`;
        dropdown.style.fontSize = `${fontSize * scale}px`;
        dropdown.style.color = textColor;
        dropdown.style.backgroundColor = hoverColor;
        dropdown.style.borderRadius = `${borderRadius * scale}px`;
        dropdown.style.zIndex = `${zIndex}`;
        dropdown.focus();
        dropdown.onchange = (ev: Event) => {
            const target = ev.target as HTMLSelectElement;

            if (!target) {
                console.error(`Cannot cast target to 'HTMLSelectElement'.`);
                Dropdown.cancelDropdown();
                return;
            }

            if (!target.value) {
                console.error(`No value selected.`);
                Dropdown.cancelDropdown();
                return;
            }

            if (!onChange(target.value)) {
                console.error(`Input value requirements not met.`);
                Dropdown.cancelDropdown();
                return;
            }

            onChange(target.value);
            this.setState({selectedIndex: target.selectedIndex});
            Dropdown.cancelDropdown();
        };

        Dropdown.dropdownElement = dropdown;
    }

    public static cancelDropdown() {
        const dropdown = Dropdown.dropdownElement;
        if (!dropdown)
            return;
        
        dropdown.onchange = null;
        dropdown.parentNode?.removeChild(dropdown);
    }

    onHover(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "pointer");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const textBG = event.currentTarget.children[0];
        textBG.setAttr("fill", this.props.hoverColor);
        layer.draw();
    }

    onHoverExit(event: KonvaEventObject<Event>) {
        KonvaUtils.setCursor(event, "default");

        const layer = event.currentTarget.getLayer();
        if (!layer) {
            console.error(`The Konva layer could not be found.`);
            return;
        }

        const textBG = event.currentTarget.children[0];
        textBG.setAttr("fill", this.props.bgColor);
        layer.draw();
    }

    render() {
        const { x, y, width, height, fontSize, fontStyle, padding, textColor, bgColor, borderRadius, values } = this.props;

        return(
            <Label name="top-bar-label"
                x={x}
                y={y}
                onMouseEnter={this.onHover}
                onMouseLeave={this.onHoverExit} 
                onClick={event => {
                    event.cancelBubble = true;
                    this.showDropdown(event);
                }}
                onTap={event => {
                    event.cancelBubble = true;
                    this.showDropdown(event);
                }}>
                <Tag fill={bgColor}
                        cornerRadius={borderRadius} />
                <Text name="top-bar-text"
                        width={width}
                        height={height}
                        fontStyle={fontStyle}
                        fontSize={fontSize}
                        verticalAlign="middle"
                        padding={padding}
                        fill={textColor}
                        text={values[this.state.selectedIndex]} />
            </Label>
        );
    }
}